import React from "react";
import { Zform, ZcolorPicker } from "zerod";
import { Input, message, InputNumber, Button, Select, DatePicker, Radio } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Zform/doc.md";
let hasHide = false;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const formItems = [
	{
		key: "radio",
		label: "单选",
		labelFocused: true,
		render: (form) => {
			return (
				<RadioGroup>
					<Radio value={1}>A</Radio>
					<Radio value={2}>B</Radio>
					<Radio value={3}>C</Radio>
					<Radio value={4}>D</Radio>
				</RadioGroup>
			);
		},
		//antd的 form.getFieldDecorator的options
		options: () => ({
			initialValue: "",
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		}),
	},
	{
		key: "datepicker",
		label: "日期框",
		render: (form) => {
			return <DatePicker />;
		},
		//antd的 form.getFieldDecorator的options
		options: () => ({
			initialValue: null,
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		}),
	},
	{
		key: "selectTest",
		label: "下拉框",
		render: (form) => {
			return (
				<Select>
					<Option value="jack">Jack</Option>
					<Option value="lucy">Lucy</Option>
					<Option value="disabled" disabled>
						Disabled
					</Option>
					<Option value="Yiminghe">yiminghe</Option>
				</Select>
			);
		},
		//antd的 form.getFieldDecorator的options
		options: () => ({
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		}),
	},
	{
		key: "serviceCode",
		label: "服务编码",
		render: (form) => {
			return new Promise((resolve) => {
				setTimeout(() => {
					resolve(<Input placeholder="请输入服务编码" />);
				}, 5000);
			});
		},
		//antd的 form.getFieldDecorator的options
		options: () => ({
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		}),
	},
	{
		key: "serviceName",
		label: "服务名称",
		render: (form) => {
			return <Input placeholder="请输入服务名称" />;
		},
		//antd的 form.getFieldDecorator的options
		options: {
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		},
	},
	{
		key: "colorValue",
		label: "颜色值",
		render: (form) => {
			return <ZcolorPicker />;
		},
		//antd的 form.getFieldDecorator的options
		options: {
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		},
	},
	{
		key: "serviceRemark",
		label: "服务说明",
		span: { md: 24 },
		render: (form) => {
			return <Input.TextArea rows={2} placeholder="请输入服务说明" />;
		},
		//antd的 form.getFieldDecorator的options
		options: {
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		},
	},
	{
		key: "servicePort",
		label: "端口号",
		labelWidth: "80px",
		render: (form) => {
			return <InputNumber min={11110} max={65535} step={10} />;
		},
		//antd的 form.getFieldDecorator的options
		options: {
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		},
	},
	{
		key: "myButton",
		label: " ",
		render: (form, changeFormItems) => {
			return (
				<Button
					type="primary"
					onClick={() => {
						// const newItems = this.items.slice(0);
						// newItems.splice(4, 1);
						// changeFormItems(hasHide ? this.items : newItems);
						changeFormItems(
							{
								key: "servicePort",
								show: hasHide,
							},
							true,
						);
						hasHide = !hasHide;
					}}
				>
					{hasHide ? "显示" : "不显示"}端口号
				</Button>
			);
		},
	},
	{
		key: "confProperty",
		label: "默认配置",
		span: 24,
		render: (form) => {
			return <Input.TextArea rows={15} placeholder="请输入默认配置" />;
		},
		//antd的 form.getFieldDecorator的options
		options: {
			//验证规则
			rules: [
				{
					required: true,
					message: "不能为空。",
				},
			],
		},
	},
];
function getDemo(labelLayout) {
	return class Myjavascript extends React.PureComponent {
		items = formItems;
		defaultValue = {
			serviceCode: "9999",
			serviceRemark: "llll",
		};
		render() {
			return (
				<Zform
					labelLayout={labelLayout}
					formDefaultValues={this.defaultValue}
					items={this.items}
					onSubmit={(values) => {
						// console.log(values);
						return Promise.resolve().then((re) => {
							message.success("提交成功：" + JSON.stringify(values));
						});
					}}
				/>
			);
		}
	}
}
export default AmdDocHOC(doc, {
	demo1: function() {
		const Myjavascript=getDemo("inline");
		return <Myjavascript />;
	},
	demo2: function() {
		const Myjavascript=getDemo("vertical");
		return <Myjavascript />;
	},
	demo3: function() {
		const Myjavascript=getDemo("horizontal");
		return <Myjavascript />;
	},
});
